  openSnackBarError(message: any) {
    this._snackBar.open(message, 'Close', {
      duration: 3000,
      verticalPosition: 'top',
      horizontalPosition: 'right',
      panelClass: ["error"]
    });
  }

  openSnackBarSuccess(message: any) {
    this._snackBar.open(message, 'Close', {
      duration: 3000,
      verticalPosition: 'top',
      horizontalPosition: 'right',
      panelClass: ["success"]
    });
  }

  openSnackBarWarning(message: any) {
    this._snackBar.open(message, 'Close', {
      duration: 3000,
      verticalPosition: 'top',
      horizontalPosition: 'right',
      panelClass: ["warning"]
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  createForm() {
    this.AcEntryForm = this.formBuilder.group({
      AcNumber: ['', Validators.required],
      // CheckDate: ['', [Validators.required, Validators.pattern("")]],
      CheckrunName: ['', [Validators.required, Validators.minLength(1)]],
    });
  }

  get AcNumber() { return this.AcEntryForm.get('AcNumber'); }
  // get CheckDate() { return this.AcEntryForm.get('CheckDate'); }
  get CheckrunName() { return this.AcEntryForm.get('CheckrunName'); }

  ngOnInit(): void {
    this.userLoggedIn = JSON.parse(localStorage.getItem('userLoggedIn')!);
    this.userRole = this.userLoggedIn.DefaultRoleId;
    this.userId = this.userLoggedIn.Id;
    this.isViewList = true;
    this.isCreate = false;
    this.isViewable = false;
    this.getTableDetails();
    this.getAccountNumber();
  }

  openCreateForm() {
    this.isViewList = false;
    this.isCreate = true;
    this.isViewable = false;
    this.createForm();
    this.AcEntryForm.reset();
    this.getAccountNumber()
  }

  getTableDetails() {
    this.rest.getAll(this.global.getapiendpoint() + "BankIn/Getall").subscribe((data: any) => {
      if (data.Success) {
        let fetchedData = data.Data;

        let gridData: any = [];

        fetchedData.forEach((element: any) => {
          let statusText = '';
          if(element.apiStatus == 1){
            statusText = 'Success'
          }
          else if(element.apiStatus == 0){
            statusText = 'In Process'
          }
          else{
            statusText = 'Failed'
          }
          gridData.push({
            Id: element.Id,
            AcNumber: element.AcNumber,
            CreatedDate: moment(element.CreatedDate).format('DD-MM-YYYY'),
            CheckrunName: element.CheckrunName,
            apiStatus : element.apiStatus,
            apiStatusText : statusText
          })
        });

        this.dataSource = new MatTableDataSource(gridData);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
    });
  }

  getAccountNumber() {
    this.rest.getAll(this.global.getapiendpoint() + "AcNo/GetBankAccountno").subscribe((data: any) => {
      if (data.Success) {
        this.appa = data.Data
      }
      else {
        this.appa = [];
      }
    });
  }
  
