  //     // }
  //     // if (this.data.Success == true) {
  //     //   this.spinner.hide();
  //     //   this.openSnackBarSuccess('Data Saved Successfully');
  //     //   this.openListPage();
  //     //   // this.UserJourney(this.userId, "Saved Button Clicked of BoE Entry", "NA", "Success");
  //     // }
  //     // else {
  //     //   this.spinner.hide();
