    roles: any = [];
    userId: any;
    allRoles: any[] = [];
    allRolesInit: any[] = [];
    filteredRoles!: Observable<any[]>;
    @ViewChild('roleInput', { static: true }) roleInput!: ElementRef<HTMLInputElement>;
    @ViewChild('autoRole', { static: true }) autoRole!: any;
    @ViewChild('roleList', { static: true }) roleList: any;
    @ViewChild('form', { static: true }) form: any;
    displayedColumns: string[] = ['Id', 'LoginId', 'EmpCode', 'EmpName', 'RoleCode', 'IsActive'];
