    @HostListener('window:keyup', ['$event']) keyEvent(event: KeyboardEvent) {

        if (event.altKey && event.keyCode == KEY_CODE.A_KEY) {
            this.addUser();
        }
        if (event.altKey && event.keyCode == KEY_CODE.B_KEY) {
            this.backUser();
        }
        if (event.altKey && event.keyCode == KEY_CODE.U_KEY) {
            // this.uploadUser();
        }
        if (event.altKey && event.keyCode == KEY_CODE.S_KEY) {
            this.saveUser();
        }
        if (event.altKey && event.keyCode == KEY_CODE.X_KEY) {
            this.exportUser();
        }
    }

    saveUser() {
        this.rest.checkDuplicate(this.global.getapiendpoint() + 'user/CheckDuplicateUser/', this.LoginId?.value.toString().trim(),
            (this.Id?.value !== '' ? this.Id?.value : '0')).subscribe((data: any) => {
                if (data.Data) {
                    // this.toastr.showNotification('top', 'right', data.Message, 'danger');
                }
                else {
                    var roleId: any = [];
                    if (this.Role?.value) {
                        this.Role.value.forEach((element: any) => { roleId.push(element.Id); });
                    }

                    var model: any = {
                        Id: this.Id?.value,
                        ADUser: this.ADUser?.value,
                        EmpCode: this.EmpCode?.value,
                        EmpName: this.EmpName?.value,
                        EmailId: this.EmailId?.value,
                        LoginId: this.LoginId?.value,
                        DefaultRoleId: roleId ? roleId[0] : "",
                        IsActive: this.IsActive?.value,
                        RoleId: roleId,
                        UserId: this.userLoggedIn.Id,
                        UserRoleId: this.userLoggedIn.DefaultRoleId
                    };

                    var apiUrl = '';
                    if (this.Id?.value == '') {
                        apiUrl = 'user/CreateUser';
                    }
                    else {
                        apiUrl = 'user/UpdateUser';
                    }
                    this.rest.create(this.global.getapiendpoint() + apiUrl, model).subscribe((data: any) => {
                        if (data.Success) {
                            this.UserJourney(this.userId, "Saved Button Clicked of User Form", "NA", "Success");
                            //this.toastr.showNotification('top', 'right', data.Message, 'success');
                        }
                        else {
                            this.UserJourney(this.userId, "Saved Button Clicked of User Form", "NA", "Failed");
                            //this.toastr.showNotification('top', 'right', data.Message, 'danger');
                        }
                        this.getAllUsers();
                    });
                }
            });
    }
    UserJourney(UserId: any, EventName: any, RequestBody: any, ResponseBody: any) {
        var model: any = {
            UserId: UserId,
            EventName: EventName,
            RequestBody: RequestBody,
            ResponseBody: ResponseBody
        };
        this.rest.create(this.global.getapiendpoint() + 'UserJourney/UserTrackJourney', model).subscribe((data: any) => {
        });
    }
