  getDestinationSubInventoryCode() {
    if (this.selectedType.length != 0) {
      let typeName = this.selectedType.name;
      if (typeName == 'SRN' || typeName == 'RMO') {
        let DescriptionValue;
        let OrganizationIdValue;
        var bodyParams;
        if (typeName == 'SRN') {
          if (this.DesORGCode?.value != null) {
            DescriptionValue = 'Stores - ' + this.DesORGCode?.value.OrganizationName + ' - SRN';
            OrganizationIdValue = this.DesORGCode?.value.OrganizationId;
            bodyParams = {
              Description: DescriptionValue,
              OrganizationId: OrganizationIdValue
            }
          }
        }

        if (typeName == 'RMO') {
          if (this.DesORGCode?.value != null) {
            let stateName = (this.DesORGCode?.value.OrganizationName).slice(4);
            DescriptionValue = 'Stores ' + stateName;
            OrganizationIdValue = this.DesORGCode?.value.OrganizationId;
            bodyParams = {
              Description: DescriptionValue,
              OrganizationId: OrganizationIdValue
            }
          }
        }

        this.rest.postParams(this.global.getapiendpoint() + "transaction/GetSubInventoryDetails", bodyParams).subscribe((data: any) => {
          if (data.success == true) {
            this.srnSubinventoryDescription = data.Data[0].Description;
            this.srnSecondaryInventoryName = data.Data[0].SecondaryInventoryName;
            this.DesSubInventoryCode?.setValue(this.srnSubinventoryDescription);
            this.isDesSubCodeDisabled = true;
            //console.log("DesSubInventoryCode",this.transactionForm.get('DesSubInventoryCode')?.value)
          }
          else {
            this.DesSubInventoryCode?.setValue('');
            this.isDesSubCodeDisabled = true;
            //console.log("DesSubInventoryCode",this.transactionForm.get('DesSubInventoryCode')?.value)
          }
        });

      }

    }
  }

